import React, { useState } from 'react'

export const AddTodo = (props) => {
    const [work, setWork] = useState("");
    const [desc, setDesc] = useState("");

    const submit=(e)=>{
        e.preventDefault();
        if(!work || !desc){
            alert('please fill all the fields')
        }
        else{
            props.addTodo(work,desc);
            setWork("");
            setDesc("");
        }
    }
    const handleChange=(event)=>{
        setWork(event.target.value);
        
    }
    const handleChanged=(event)=>{
        setDesc(event.target.value);
    }

    return (
        <div className="container">
            <h3 style={{    marginTop:'20px',marginBottom:'26px'}}>Add To-Do</h3> 
            <form onSubmit={submit}>
            <div className="mb-3">
                <label htmlFor="work" className="form-label">ToDo Who you Are?</label>
                <input type="text" className="form-control" id="work" value={work} onChange={handleChange}/>
                
            </div>
            <div className="mb-3">
                <label htmlFor="desc" className="form-label">ToDo Description</label>
                <input type="text" className="form-control" id="desc" value={desc} onChange={handleChanged}/>
            </div>
            
            <button type="submit" className="btn btn-sm btn-success">ADD TOdO</button>
            </form>
        </div>
    )
}

export default AddTodo;