import React from 'react'
import TodoItem from './TodoItem'

export default function Todos(props) {
    const myStyle={
        minHeight:'70vh',
        margin:'40px auto'
    }
    return (
        <div id="container my-3" style={myStyle}>
            <h3 className="my-3" style={{marginLeft:'300px'}}>ToDolist</h3>
            {props.todos.length===0?<p style={{marginLeft:'300px'}}>No Todo To Display</p> :
            props.todos.map((todo)=>{
                return (<TodoItem todo={todo} key={todo.id} onDelete={()=>{props.onDelete(todo)}}/> )
            })}
            {/* <TodoItem todo={props.todos[0]}/> */}



        </div>
    )
}
